
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.modal-pc {
  height: 500px;
  margin: auto 0;
}
.modal-phone {
  width: 100%;
  margin: auto 0;
}
.modal-pc img {
  height: 100%;
}
.modal-phone img {
  width: 100%;
}



